@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
html {
  background-image: linear-gradient(to bottom right, #304ffe, #8c9eff);
}

.app {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr 1fr;
  grid-template-areas:
  "game"
  "answers";
  overflow-y: hidden;
}

.game-button {
  font-family: 'Poppins', sans-serif;
  padding: .5em 1.5em;
  border-radius: 3em;
  background-color: #424242;
  color: white;
  border: 2px solid transparent;
  margin: 15px 30px;
}

button:focus {
  outline: none;
}

.game-button:hover {
  border: 2px solid white;
  background-color: #689f38;
  color: white;
}

.button-div {
  display: flex;
  justify-content: center;
}

.app {
  height: 100vh;
}

.stats-panel {
  display: flex;
  justify-content: center;
}

h2 {
  margin: 0 30px;
  color: white;
}
hr {
  color: white;
  width: 100vw;
  height: 3px;
}

.game-area {
  margin: auto;
  grid-area: game;
}
.word-area {
  grid-area: answers;
  overflow-y: auto;
}

.green {
  border: 1px solid green;
}


.letter {
    box-sizing: border-box;
    height: 50px;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #fbc02d;
    border-radius: 8px;
    font-family: 'Poppins', sans-serif;
    font-size: 1.3rem;
    background-image: linear-gradient(to bottom right, #fff176, #fbc02d);
    color: #424242;
}

.selected {
    background-image: linear-gradient(to bottom right, #a5d6a7, #4caf50);
    border-color: #4caf50;
}
.letter-row {
    display: flex;
    width: 90%;
    margin: auto;
    justify-content: space-between;
}

.board {
    width: 300px;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: auto;
}
.guess-div {
    text-align: center;
    min-height: 4rem;
}
.guess {
    font-family: 'Open Sans', sans-serif;
    font-size: 3rem;
    letter-spacing: 8px;
}
.words-div {
    display: flex;
    max-width: 100vw;
    padding: 0 32px;
    flex-wrap: wrap;
    height: auto;
    justify-content: center;
    margin: 16px auto;
}

.word {
    padding: 0 1em;
    border: 2px solid #ffab00;
    background-color: #ffd740;
    border-radius: 0;
    color: #424242;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    margin: 8px 8px;
    letter-spacing: 1px
}

.hidden {
    display: none;
}

.correct {
    background-color: #4caf50;
    border-color: #2E7D32;
    color: white;
}
